<template>
    <div>
        <top-nav title="拼团活动列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning"  v-loading="loading" v-if="datas.groupbuy">
            <el-row>
                <el-col :span="4">商品编号：{{datas.groupbuy.goods_sn}}</el-col>
                <el-col :span="12">商品名称：{{datas.groupbuy.goods_name}}</el-col>
            </el-row>
            <el-row>
                <el-col :span="4">多少人成团：{{datas.groupbuy.need_num}}</el-col>
                <el-col :span="6">成团时长(小时)：{{datas.groupbuy.end_time}}</el-col>
            </el-row>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item label="用户ID">
                        <el-input v-model="searchForm.user_id" placeholder="用户ID"></el-input>
                    </el-form-item>

                    <el-form-item label="收货人">
                        <el-input v-model="searchForm.name" placeholder="收货人"></el-input>
                    </el-form-item>

                    <el-form-item label="联系方式">
                        <el-input v-model="searchForm.mobile" placeholder="联系方式"></el-input>
                    </el-form-item>

                    <el-form-item label="当前已达到(包括超过)拼团次数">
                        <el-input v-model="searchForm.now_num" placeholder="拼团次数"></el-input>
                    </el-form-item>

                    <el-form-item label="开团时间">
                        <el-date-picker v-model="searchForm.add_time" type="datetime" placeholder="开砍时间"></el-date-picker>
                    </el-form-item>

                    <el-form-item label="失效时间">
                        <el-date-picker v-model="searchForm.end_time" type="datetime" placeholder="失效时间"></el-date-picker>
                    </el-form-item>

                    <el-form-item label="是否已失效">
                        <el-select v-model="searchForm.is_invalid" placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option label="未失效" :value="0"></el-option>
                            <el-option label="已失效" :value="1"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="是否已成团">
                        <el-select v-model="searchForm.is_success" placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option label="未成团" :value="0"></el-option>
                            <el-option label="已成团" :value="1"></el-option>
                        </el-select>
                    </el-form-item>

                    <div style="padding-left:20px;">
                        <el-button size="mini" type="primary" @click="getList(1)">搜索</el-button>
                        <el-button size="mini" type="text" @click="searchForm.user_id='',searchForm.name='',
                        searchForm.mobile='',searchForm.now_num='',searchForm.end_time='',searchForm.add_time='',
                        searchForm.is_invalid=-999,searchForm.is_success=-999,getList(1)">取消搜索</el-button>
                    </div>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" size="small">
                    <el-table-column prop="groupbuy_item_id" label="开团ID" width="60"></el-table-column>

                    <el-table-column prop="user_id" label="用户ID"></el-table-column>

                    <el-table-column prop="name" label="收货人"></el-table-column>

                    <el-table-column prop="mobile" label="联系方式"></el-table-column>

                    <el-table-column prop="need_num" label="参团人数/需要人数"  width="180">
                        <template slot-scope="scope">
                            {{scope.row.now_num}} / {{scope.row.need_num}}
                        </template>
                    </el-table-column>

                    <el-table-column prop="add_time" label="开团时间" width="240"></el-table-column>

                    <el-table-column prop="end_time" label="过期时间" width="240"></el-table-column>

                    <el-table-column prop="is_invalid" label="状态" >
                        <template slot-scope="scope">
                            {{scope.row.is_invalid == 1 ? '已失效' : '正常'}}
                        </template>
                    </el-table-column>

                    <el-table-column prop="is_success" label="是否拼团成功" >
                        <template slot-scope="scope">
                            {{scope.row.is_success == 1 ? '已成团' : '未成团'}}
                        </template>
                    </el-table-column>

                    <el-table-column prop="is_vip" label="VIP特价" >
                        <template slot-scope="scope">
                            {{scope.row.is_vip == 1 ? '是' : '否'}}
                        </template>
                    </el-table-column>

                    <el-table-column label="操作" >
                        <template slot-scope="scope">
                            <el-col :span="8">
                                <el-button size="mini" type="text" @click="showLog(scope)">查看参团记录</el-button>
                            </el-col>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
        <el-dialog title="参团记录" :visible.sync="dialog" width="1200px" center :modal-append-to-body="false">
            <el-table :data="joinData.list" style="width: 100%" size="small">
                <el-table-column prop="user_id" label="用户ID" width="60"></el-table-column>

                <el-table-column label="团长" width="60">
                    <template slot-scope="scope">
                        {{scope.row.is_tuanzhang ? '是' : '否'}}
                    </template>
                </el-table-column>

                <el-table-column prop="name" label="收货人" width="100"></el-table-column>

                <el-table-column prop="mobile" label="联系方式" width="100"></el-table-column>

                <el-table-column prop="bar_code" label="规格码" width="100"></el-table-column>

                <el-table-column prop="key_name" label="规格名称" width="300"></el-table-column>

                <!--<el-table-column label="价格" width="100">-->
                    <!--<template slot-scope="scope">-->
                        <!--￥ {{scope.row.goods_price}}-->
                    <!--</template>-->
                <!--</el-table-column>-->

                <!--<el-table-column label="邮费" width="100">-->
                    <!--<template slot-scope="scope">-->
                        <!--￥ {{scope.row.shipping_price}}-->
                    <!--</template>-->
                <!--</el-table-column>-->

                <el-table-column prop="pay_type_mean" label="支付方式" ></el-table-column>

                <el-table-column prop="pay_time" label="支付时间" ></el-table-column>

            </el-table>

            <div class="page">
                <div class="page-mian">
                    <el-pagination @current-change="getList" :page-size="datas.limit"
                                   layout="total, prev, pager, next" :total="datas.count">
                    </el-pagination>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import DateBetween from "@/components/DateBetween";

    export default {
        name: 'goodsAttrIndex',
        components: {TopNav,DateBetween},
        data() {
            return {
                groupbuy_id:0,
                //整体菊花
                loading: false,
                typeLoading: false,
                subLoading: false,
                dialog: false,
                joinpage: 1,
                //列表数据
                datas: {},
                //搜索数据
                searchForm: {
                    user_id: '',
                    name: '',
                    mobile: '',
                    now_num: '',
                    add_time: '',
                    end_time: '',
                    is_invalid: -999,
                    is_pay: -999,
                    is_success: -999,
                },
                joinData: {
                    list: [],
                },
            }
        },

        created() {
            this.groupbuy_id = this.$route.params.id;
            this.getList();
        },

        methods: {
            getList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.marketing.groupbuy/join", {
                    appToken: this.$store.getters.appToken,
                    groupbuy_id: this.$route.params.id,
                    user_id: this.searchForm.user_id,
                    name: this.searchForm.name,
                    mobile: this.searchForm.mobile,
                    now_num: this.searchForm.now_num,
                    add_time: this.searchForm.add_time,
                    end_time: this.searchForm.end_time,
                    is_invalid: this.searchForm.is_invalid,
                    is_success: this.searchForm.is_success,
                    page: page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },


            showLog(scope,page = 1) {
                this.dialog = true;
                this.subLoading = true;
                console.log(scope.row.groupbuy_item_id);
                api.post("/shop/admin.marketing.groupbuy/joinlist", {
                    appToken: this.$store.getters.appToken,
                    groupbuy_item_id: scope.row.groupbuy_item_id,
                    page: page,
                }).then(res => {
                    this.joinData = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

        }
    }
</script>

